html {
  position: relative;
  min-width: none;

  background: #16171b;
  background-image: url(/EarthSaviours/static/media/Home.e907e907.png);
  background-repeat: no-repeat;
  background-size: cover;
}

.navbar {
  height: 80px;
  display: flex;
  flex-direction: row;
  justify-content: space-around;
  align-items: center;
  font-size: 10px;

  /* z-index: 999;*/
}

.navbar__nav {
  display: flex;
  justify-content: center;
  flex-direction: row;
  align-items: center;
  margin: 0 0rem;
  border-radius: 10px;
  font-family: "Red Hat Display";
  font-weight: 100;
  text-decoration: none;
}

.navbar-menu-logo {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-family: "Red Hat Display", sans-serif;
  font-weight: 600;
  font-size: 20px;
  text-decoration: none;
  color: #ffffff;
  padding-left: 0px;
  padding-right: 105px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.navbar-logo {
  color: #c1ffe5;
  justify-self: start;
  margin-left: 0px;
  cursor: pointer;
  height: 40px;
  width: 56px;
  margin-top: 0px;
  margin-left: 0px;
  margin-right: 20px;
}

.navbar-container {
  display: flex;
  flex-direction: row;
  align-items: center;
  justify-content: stretch;
}

.navbar-menu {
  font-family: "Red Hat Display", sans-serif;
  justify-content: center;
  font-weight: 600;
  font-size: 1rem;
  text-decoration: none;
  color: #ffffff;
  padding-left: 25px;
  padding-right: 35px;
  padding-top: 10px;
  padding-bottom: 10px;
}

.navbar-menu-links {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  font-family: "Red Hat Display", sans-serif;
  justify-content: center;
  font-weight: 600;
  font-size: 1rem;
  text-decoration: none;
  color: #ffffff;
  padding-left: 200px;
  padding-right: 0px;
  padding-top: 10px;
  padding-bottom: 10px;
}

button {
  height: 35px;
  width: 150px;
  background: rgba(0, 0, 0, 0.03);
  border-radius: 12px;
  font-family: "Red Hat Display", sans-serif;
  color: #ffffff;
  font-weight: 600;
  font-size: 1rem;
}

.navbar-menu:hover {
  color: #70e4e6;
  transition: all 0.2s ease-out;
}

@media screen and (max-width: 960px) {
  .NavbarItems {
    position: relative;
  }
}
